import {Component, OnInit, Input} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {first} from 'rxjs/operators';

import {Settings} from '../../_models/settings';
import {Sensor} from '../../_models/sensor';
import {RespResult} from 'src/app/_models/resp.result';
import {ActivatedRoute, Router} from '@angular/router';

import {SensorService} from '../../_services/sensor.service';

// import {AuthenticationService} from '../../_services/authentication.service';

@Component({
    selector: 'app-sensor-settings',
    templateUrl: './sensor-settings.component.html',
    styleUrls: ['./sensor-settings.component.css']
})
export class SensorSettingsComponent implements OnInit {
    @Input() sensor: Sensor;
    settings: Settings;
    updateForm: FormGroup;
    loading = false;
    submitted = false;
    error = '';
    // checked: {
    //     showX: boolean;
    // };

    constructor(
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private sensorService: SensorService
    ) {
    }

    ngOnInit(): void {
        this.settings = this.sensor.settings;

        this.updateForm = this.formBuilder.group({
            showX: [this.settings.showX, Validators.required],
            showY: [this.settings.showY, Validators.required],
            showZ: [this.settings.showZ, Validators.required],
            overlap: [this.settings.overlap, Validators.required],
            freqLow: [this.settings.freqLow, Validators.required],
            freqHigh: [this.settings.freqHigh, Validators.required],
            name: [this.sensor.name, Validators.required],
            class: [this.sensor.class, Validators.required],
            location: [this.sensor.location, Validators.required]
        });
    }

    // tslint:disable-next-line:typedef
    get f() {
        return this.updateForm.controls;
    }

    // tslint:disable-next-line:typedef
    onSubmit() {
        this.submitted = true;

        // stop here if form is invalid
        if (this.updateForm.invalid) {
            return;
        }

        const updateSettingJson = [
            {
                propName: 'name',
                value: this.f.name.value,
            },
            {
                propName: 'class',
                value: this.f.class.value,
            },
            {
                propName: 'location',
                value: this.f.location.value,
            },
            {
                propName: 'settings',
                value: {
                    showX: this.f.showX.value,
                    showY: this.f.showY.value,
                    showZ: this.f.showZ.value,
                    overlap: this.f.overlap.value,
                    freqLow: this.f.freqLow.value,
                    freqHigh: this.f.freqHigh.value
                }
            }
        ];

        this.loading = true;
        this.sensorService.updateSettings(this.sensor._id, JSON.stringify(updateSettingJson))
            .pipe(first())
            .subscribe({
                next: (result: RespResult) => {
                    // @ts-ignore
                    console.log(`result.ok = ${result.result.ok}`);
                    this.loading = false;
                },
                error: error => {
                    this.error = error;
                    this.loading = false;
                }
            });
        this.settings.showX = this.f.showX.value;
        this.settings.showY = this.f.showY.value;
        this.settings.showZ = this.f.showZ.value;
        this.settings.overlap = this.f.overlap.value;
        this.settings.freqLow = this.f.freqLow.value;
        this.settings.freqHigh = this.f.freqHigh.value;
        this.sensor.name = this.f.name.value;
        this.sensor.location = this.f.location.value;
        this.sensor.class = this.f.class.value;
    }

    removeIcon(): void {
        this.error = '';
    }

    // check(item: string): void {
    //     if (item === 'showX') {
    //         this.settings.showX = !this.settings.showX;
    //         console.log('clicked showX: ' + this.settings.showX);
    //     } else if (item === 'showY') {
    //         this.settings.showY = !this.settings.showY;
    //         console.log('clicked showY: ' + this.settings.showY);
    //     } else if (item === 'showZ') {
    //         this.settings.showZ = !this.settings.showZ;
    //         console.log('clicked showZ: ' + this.settings.showZ);
    //     } else if (item === 'overlap') {
    //         this.settings.overlap = !this.settings.overlap;
    //         console.log('clicked overlap: ' + this.settings.overlap);
    //     }
    // }
    // check(item: string): void {
    // }

}
