import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';

import {environment} from 'src/environments/environment';
import {RespResult} from 'src/app/_models/resp.result';
import {SensorInfo} from 'src/app/_models/sensor.info';

@Injectable({providedIn: 'root'})
export class SensorService {
    sensorInfo: SensorInfo;

    constructor(private http: HttpClient) {
    }

    getAll(): any {
        console.log('running get all sensors');
        return this.http.get<SensorInfo>(`${environment.apiUrl}/sensors`);
    }

    updateSettings(sensorId: string, settings: string): any {
        console.log('Start update settings');
        return this.http.put<RespResult>(`${environment.apiUrl}/sensors/${sensorId}`, settings);
    }

    getSensorInfo(): SensorInfo {
        return this.sensorInfo;
    }

    setSensorInfo(sensorInfo: SensorInfo): void {
        this.sensorInfo = sensorInfo;
    }

}
