import {Component, OnInit, Input, OnChanges, SimpleChanges} from '@angular/core';

// import {Sensor} from '../../_models/sensor';
import {DataService} from '../../_services/data.service';
// import {DataPoints} from 'src/app/_models/data.points';
import {DataPoint} from 'src/app/_models/data.point';

@Component({
    selector: 'app-data-get-range',
    templateUrl: './data-get-range.component.html',
    styleUrls: ['./data-get-range.component.css']
})
export class DataGetRangeComponent implements OnInit, OnChanges {
    @Input() sensorId: string;
    @Input() start: string;
    @Input() end: string;
    loading: boolean;
    dataPoints: DataPoint[];
    dataNumber: number;

    constructor(private dataService: DataService) {
    }

    ngOnInit(): void {
        this.loading = false;
        this.dataNumber = 1;
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (typeof this.start === 'undefined' || typeof this.end === 'undefined') {
            return;
        }
        this.dataNumber = 0;
        this.loading = true;
        this.dataService.getRange(this.sensorId, this.start, this.end).subscribe(dataInfo => {
            console.log(dataInfo);
            const cnt = 'count';
            this.dataNumber = dataInfo[cnt];
            console.log(`dataInfo[cnt]: ${this.dataNumber}`);
            if (!dataInfo[cnt]) {
                this.loading = false;
                return false;
            }
            this.loading = false;
            const dps = 'Datapoints';
            this.dataPoints = dataInfo[dps];
        });
    }

}
