import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';

import {environment} from 'src/environments/environment';
import {User} from 'src/app/_models/user';
import {RespResult} from '../_models/resp.result';

@Injectable({providedIn: 'root'})
export class UserService {
    user: User;

    constructor(private http: HttpClient) {
    }

    getAll(): any {
        console.log('running get all users');
        return this.http.get<User[]>(`${environment.apiUrl}/users`);
    }

    getUser(): any {
        if (this.user) {
            return this.user;
        }
        return null;
    }

    setUser(user: User): void {
        this.user = user;
    }

    updateSettings(settings: string): any {
        console.log('Start update settings');
        return this.http.put<RespResult>(`${environment.apiUrl}/users/update`, settings);
    }
}
