import {Component, OnInit} from '@angular/core';
import {first} from 'rxjs/operators';

import {SensorInfo} from 'src/app/_models/sensor.info';
import {Sensor} from 'src/app/_models/sensor';
import {SensorService} from 'src/app/_services/sensor.service';
// import { FilterPipe } from '../../_helpers/filter.pipe';

@Component({
    selector: 'app-equipment',
    templateUrl: './equipment.component.html',
    styleUrls: ['./equipment.component.css']
})
export class EquipmentComponent implements OnInit {
    loading = true;
    sensorInfo: SensorInfo;
    sensors: Sensor[];
    sensorNumber: number;
    searchClass: string;
    // sensorId: string;
    // chart: boolean;

    constructor(private sensorService: SensorService) {
    }

    ngOnInit(): void {
        this.loading = true;
        this.sensorInfo = this.sensorService.getSensorInfo();
        if (this.sensorInfo) {
            const sensorskey = 'sensors';
            this.sensors = this.sensorInfo[sensorskey];
            const numberkey = 'count';
            this.sensorNumber = this.sensorInfo[numberkey];
            this.loading = false;
        }
        else {
            this.updateInfo();
        }
    }

    updateInfo(): void {
        this.loading = true;
        this.sensorService.getAll().pipe(first()).subscribe(sensorInfo => {
            console.log(sensorInfo);
            this.sensorInfo = sensorInfo;
            this.sensorService.setSensorInfo(sensorInfo);
            const sensorskey = 'sensors';
            this.sensors = sensorInfo[sensorskey];
            const numberkey = 'count';
            this.sensorNumber = sensorInfo[numberkey];
            this.loading = false;
        });
    }

    showChart(sensor): void {
        // @ts-ignore
        sensor.showChart = !sensor.showChart;
    }

    showSettings(sensor): void {
        sensor.showSettings = !sensor.showSettings;
    }

    showId(sensor): void {
        sensor.showId = sensor.showId !== true;
    }
}
