import {Component, OnInit} from '@angular/core';

import {AuthenticationService} from '../_services/authentication.service';
import {User} from '../_models/user';
import {Router} from '@angular/router';

@Component({
    selector: 'app-desktop-application',
    templateUrl: './desktop-application.component.html',
    styleUrls: ['./desktop-application.component.css']
})
export class DesktopApplicationComponent implements OnInit {
    activeRouter: number;
    title = 'Feili Control';
    currentUser: User;

    constructor(
        private router: Router,
        private authenticationService: AuthenticationService) {
        this.authenticationService.currentUser.subscribe(x => this.currentUser = x);
    }

    ngOnInit(): void {
    }

    setRouter(activeRouter: number): void {
        this.activeRouter = activeRouter;
    }

    logout(): any {
        this.authenticationService.logout();
        this.router.navigate(['/desktop/login']);
    }
}
