import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {PageNotFoundComponent} from './page-not-found/page-not-found.component';
import {LoginComponent} from './login/login.component';
import {SignupComponent} from './signup/signup.component';
import {ApplicationComponent} from './private-components/application/application.component';
import {AboutComponent} from './about/about.component';
import {HomeComponent} from './home/home.component';
import {EquipmentComponent} from './private-components/equipment/equipment.component';
import {SettingsComponent} from './private-components/settings/settings.component';
import {InfoComponent} from './private-components/info/info.component';
import {SensorComponent} from './private-components/sensor/sensor.component';
import {AdminLoginComponent} from './admin-components/login/login.component';
import {AdminSignupComponent} from './admin-components/signup/signup.component';
import {AdminControlComponent} from './admin-components/admin-control/admin-control.component';
import {AdminHomeComponent} from './admin-components/admin-home/admin-home.component';
import {UserFrameComponent} from './user-frame/user-frame.component';
import {AdminAddCodeComponent} from './admin-components/admin-add-code/admin-add-code.component';
import {DesktopApplicationComponent} from './desktop-application/desktop-application.component';
import {YourGuard} from './_helpers/your.guard';
import {YourGuard as DesktopGuard} from './_helpers/desktop.guard';
import {AdminGuard} from './_helpers/admin.guard';

const routes: Routes = [
    {path: '', redirectTo: 'user', pathMatch: 'full'},
    {
        path: 'user', component: UserFrameComponent,
        children: [
            {path: '', component: HomeComponent},
            {path: 'home', component: HomeComponent},
            {
                path: 'application', component: ApplicationComponent, canActivate: [YourGuard],
                children: [
                    {path: '', component: InfoComponent},
                    {path: 'info', component: InfoComponent},
                    {path: 'settings', component: SettingsComponent},
                    {path: 'equipment', component: EquipmentComponent},
                    {path: 'sensor/:sensorId', component: SensorComponent},
                ]
            },
            {path: 'about', component: AboutComponent},
            {path: 'login', component: LoginComponent},
            {path: 'signup', component: SignupComponent},
        ]
    },
    // {path: '', component: HomeComponent},
    // {path: 'home', component: HomeComponent},
    // {
    //     path: 'application', component: ApplicationComponent, canActivate: [YourGuard],
    //     children: [
    //         {path: '', component: InfoComponent},
    //         {path: 'info', component: InfoComponent},
    //         {path: 'settings', component: SettingsComponent},
    //         {path: 'equipment', component: EquipmentComponent},
    //         {path: 'sensor/:sensorId', component: SensorComponent},
    //     ]
    // },
    // {path: 'about', component: AboutComponent},
    // {path: 'login', component: LoginComponent},
    // {path: 'signup', component: SignupComponent},
    {
        path: 'admin', component: AdminHomeComponent,
        children: [
            {path: '', component: AdminControlComponent},
            {path: 'login', component: AdminLoginComponent},
            {path: 'signup', component: AdminSignupComponent},
            {
                path: 'control', component: AdminControlComponent, canActivate: [AdminGuard],
                children: [
                    {path: 'add-registration-code', component: AdminAddCodeComponent},
                ]
            }
        ]
    },
    {
        path: 'desktop', component: DesktopApplicationComponent,
        children: [
            {path: '', component: InfoComponent, canActivate: [DesktopGuard]},
            {path: 'login', component: LoginComponent},
            {path: 'signup', component: SignupComponent},
            {path: 'info', component: InfoComponent, canActivate: [DesktopGuard]},
            {path: 'settings', component: SettingsComponent, canActivate: [DesktopGuard]},
            {path: 'equipment', component: EquipmentComponent, canActivate: [DesktopGuard]},
            {path: 'sensor/:sensorId', component: SensorComponent, canActivate: [DesktopGuard]},
        ]
    },
    {path: '**', component: PageNotFoundComponent},
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
