import {Component} from '@angular/core';
import {Router} from '@angular/router';

import {AuthenticationService} from './_services/authentication.service';
import {AuthenticationService as AdminAuthenticationService} from 'src/app/_services/admin.authentication.service';
import {User} from './_models/user';
import {Admin} from './_models/admin';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'routing-app';
  currentUser: User;
  currentAdminUser: Admin;

  constructor(
    private router: Router,
    private authenticationService: AuthenticationService,
    private adminauthenticationService: AdminAuthenticationService
  ) {
    this.authenticationService.currentUser.subscribe(x => this.currentUser = x);
    this.adminauthenticationService.currentUser.subscribe(x => this.currentAdminUser = x);
  }

  logout(): any {
    this.authenticationService.logout();
    this.router.navigate(['/login']);
  }

  adminLogout(): any {
    this.adminauthenticationService.logout();
    this.router.navigate(['/admin/login']);
  }
}
