import {Component, OnInit, Input, AfterViewInit} from '@angular/core';

import {DataService} from '../../_services/data.service';
import {DataPoints} from 'src/app/_models/data.points';

@Component({
    selector: 'app-data-chart',
    templateUrl: './data-chart.component.html',
    styleUrls: ['./data-chart.component.css']
})
export class DataChartComponent implements OnInit, AfterViewInit {
    @Input() dataPointId: string;
    dataPoints: DataPoints;
    loading: boolean;
    emptyData: boolean;
    _id: string;
    time: string;
    data: [[number]];
    ready: boolean;

    constructor(private dataService: DataService) {
    }

    // tslint:disable-next-line:typedef
    ngOnInit() {
        this.ready = false;
        this.loading = true;
        this.emptyData = true;
    }

    ngAfterViewInit(): void {
        setTimeout(() => {
            this.ready = true;
            this.dataService.getData(this.dataPointId).subscribe(dataPoints => {
                console.log(dataPoints);
                const dptKey = 'dataPoint';
                this.dataPoints = dataPoints[dptKey];
                const datakey = 'data';
                this.data = this.dataPoints[datakey];
                const timekey = 'time';
                this.time = this.dataPoints[timekey];
                const _idkey = '_id';
                this._id = this.dataPoints[_idkey];
                this.emptyData = false; // Show data because it's not empty
                this.loading = false; // this must be after emptyData, or it will flash
            });
        }, 0);
    }
}
