import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {first} from 'rxjs/operators';

import {User} from '../../_models/user';
import {UserService} from '../../_services/user.service';
import {RespResult} from '../../_models/resp.result';

@Component({
    selector: 'app-settings',
    templateUrl: './settings.component.html',
    styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
    user: User;
    updateForm: FormGroup;
    loading = false;
    submitted = false;
    error = '';
    // date: string;

    constructor(private formBuilder: FormBuilder,
                private userService: UserService) {
    }

    ngOnInit(): void {
        this.loading = true;
        this.user = this.userService.getUser();
        this.updateForm = this.formBuilder.group({
            name: ['...', Validators.required],
            company: ['...', Validators.required],
            remarks: ['...', Validators.required],
        });
        if (!this.user) {
            this.userService.getAll().pipe(first()).subscribe(users => {
                this.user = users[0];
                this.userService.setUser(this.user);
                this.updateForm = this.formBuilder.group({
                    name: [this.user.user.name, Validators.required],
                    company: [this.user.user.company, Validators.required],
                    remarks: [this.user.user.remarks, Validators.required],
                });
                this.loading = false;
            });
        } else {
            // this.date = new Date(this.user.user['registerTime']).toLocaleString();
            this.updateForm = this.formBuilder.group({
                name: [this.user.user.name, Validators.required],
                company: [this.user.user.company, Validators.required],
                remarks: [this.user.user.remarks, Validators.required],
            });
            this.loading = false;
        }
    }

    // tslint:disable-next-line:typedef
    get f() {
        return this.updateForm.controls;
    }

    // tslint:disable-next-line:typedef
    onSubmit() {
        this.submitted = true;

        // stop here if form is invalid
        if (this.updateForm.invalid) {
            return;
        }

        const updateSettingJson = [
            {
                propName: 'name',
                value: this.f.name.value,
            },
            {
                propName: 'company',
                value: this.f.company.value,
            },
            {
                propName: 'remarks',
                value: this.f.remarks.value,
            }
        ];

        this.loading = true;
        this.userService.updateSettings(JSON.stringify(updateSettingJson))
            .pipe(first())
            .subscribe({
                next: (result: RespResult) => {
                    // @ts-ignore
                    console.log(`result.ok = ${result.result.ok}`);
                    this.loading = false;
                },
                error: error => {
                    this.error = error;
                    this.loading = false;
                }
            });
        this.user.user.name = this.f.name.value;
        this.user.user.company = this.f.company.value;
        this.user.user.remarks = this.f.remarks.value;
    }


    removeIcon(): void {
        this.error = '';
    }

}
